<template>
    <div id="loginborder">
        <img src="../assets/phone.jpg" alt="" id="bg">

        <div id="inner">
            <h2>登录界面</h2>
            <br>
            用户名<el-input v-model="user" id="user" placeholder="请输入用户名"></el-input><br>
            密码<el-input show-password v-model="pass" id="pass" placeholder="请输入密码"></el-input><br>
            <el-button id="btn" @click="login" type="primary" style="margin-top:10px">登录</el-button>
        </div>
        
        

    </div>
</template>

<script>
import Router from '../router/index'
export default {
    data(){
        return{
            user:'',
            pass:''
        }
    },
    methods:{
        login(){
            this.$axios({
                url:'http://101.71.247.41:2138/login?'+'user='+this.user+'&pass='+this.pass,
                method:'get',
            }).then(res=>{
                
                if(res.data == '1'){
                    console.log(Router)
                    alert('登陆成功！')
                    sessionStorage.user = this.user;
                    Router.push({path:'/'})
                }else{
                    alert('用户名或密码错误！')
                }
            })
        }
    }
}
</script>

<style scoped>
    #loginborder{
        width: 375px;
        height:812px;
        margin: auto;
    }
    #inner{
    margin-top: 45px;
    margin-left: 35px;
    
    }
    #bg{
    position: absolute;
    top: 0;
    z-index: -1;
    }

</style>


